import React, {useState} from 'react';
import PropTypes from 'prop-types';
import creditTypes from './credit-types';
import getClasses from '../../utils/getClasses';
import {block} from './calculator';
import NumberContainer from './number/container';

function StepOne({onChange, id}) {
  const [isVisible, setIsVisible] = useState(false);
  const handleVisibilty = () => {
    setIsVisible((v) => !v);
  };

  const handleClick = (e) => {
    const {id: newId} = e.target.dataset;
    setIsVisible(false);

    if (!newId) {
      onChange(null);
    } else {
      onChange(Number(newId));
    }
  };

  return (
    <NumberContainer>
      <button
        type="button"
        className={getClasses({
          block,
          element: `field`,
          modifiers: isVisible ? [`first`] : [],
        })}
        onClick={handleVisibilty}
      >
        {!creditTypes[id] ? `  ` : creditTypes[id].title}
      </button>

      {isVisible && (
        <ul className={`list ${getClasses({block, element: `options`})}`}>
          {creditTypes
            .filter(({id: i}) => i !== id)
            .map(({title, id: i}, index, arr) => {
              const modifiers = [`option`];
              if (index === arr.length - 1) {
                modifiers.push(`last`);
              }
              return (
                <li key={i}>
                  <button
                    type="button"
                    className={getClasses({
                      block,
                      element: `field`,
                      modifiers,
                    })}
                    onClick={handleClick}
                    data-id={i}
                  >
                    {title}
                  </button>
                </li>
              );
            })}
        </ul>
      )}
    </NumberContainer>
  );
}

StepOne.propTypes = {
  onChange: PropTypes.func.isRequired,
  id: PropTypes.number,
};

export default StepOne;
